body, html {
    height: 100vh;
    margin: 0;
  }
  
  .container {
    position: relative;
    text-align: center;
    color: white;
      /* The image used */
    background-image: url("error.png");
  
    /* Full height */
    height: calc(100vh - 64px);
    margin: 0;
  
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .heroContent {
      padding: 25px;
  }